import { getOrderDetails } from '../../utils/api';

export const GET_INGREDIENTS = 'ADD_INGREDIENTS'; //      
export const GET_ORDER_REQUEST = 'GET_ORDER_REQUEST'; // 
export const GET_ORDER_SUCCESS = 'GET_ORDER_SUCCESS'; // 
export const GET_ORDER_FAILED = 'GET_ORDER_FAILED'; //  
export const VIEWING_ORDER_ENABLED = 'VIEWING_ORDER_ENABLED'; //   
export const VIEWING_ORDER_DISABLED = 'VIEWING_ORDER_DISABLED'; //   
export const ADD_SELECTED_ING = 'ADD_SELECTED_ING'; // 
export const DELETE_ING = 'DELETE_ING'; //  TOGGLE_LIST
export const TOGGLE_ING = 'TOGGLE_LIST'; // 

export function getOrder(idArrSelected) {
  return function (dispatch) {
    dispatch({
      type: GET_ORDER_REQUEST,
    });
    getOrderDetails(idArrSelected)
      .then((res) => {
        dispatch({
          type: GET_ORDER_SUCCESS,
          order: res.order,
        });
      })

      .catch((error) => {
        dispatch({
          type: GET_ORDER_FAILED,
        });
      });
  };
}
